import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardRoutingModule } from './dashboard-routing.module';
import { HttpModule, JsonpModule } from '@angular/http';
import { SharedModule } from './../shared/shared.module';
import { SharedLazyModule } from './../shared/shared-lazy.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
//components
import { DashboardComponent } from './dashboard.component';
import { DSecureComponent } from "./components/3d-secure/3d-secure.component";
import { TransferComponent } from "./components/transfer/transfer.component";

import { AmountInputComponent } from './components/transfer/amount-input/amount-input.component';
import { CardTypeComponent } from './components/transfer/card-type/card-type.component';
import { CardNumberComponent } from './components/transfer/card-number/card-number.component';
import { ExpirationDateComponent } from './components/transfer/expiration-date-input/expiration-date-input.component';
import { CardCodeComponent } from './components/transfer/card-code-input/card-code-input.component';
//pipes & directives
import { CurrencyMaskPipe } from './components/transfer/amount-input/pipe-currency-mask.pipe'
import { CurrencyFormatterDirective } from './components/transfer/amount-input/pipe-currency-mask.directive';
import { MyCardNumberFormatterDirective } from './components/transfer/card-number/pipe-card-number.directive';
import { MyCardNumberPipe } from './components/transfer/card-number/pipe-card-number.pipe';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

@NgModule({
  imports: [
    CommonModule,
    SharedLazyModule,
    DashboardRoutingModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    ReactiveFormsModule,
    NgbModule.forRoot()
  ],
  declarations: [
    DashboardComponent,
    DSecureComponent,
    AmountInputComponent,
    TransferComponent,
    CardTypeComponent,
    CardCodeComponent,
    ExpirationDateComponent,
    CurrencyFormatterDirective,
    MyCardNumberFormatterDirective,
    CurrencyMaskPipe,
    MyCardNumberPipe,
    CardNumberComponent
  ],
  providers: [
    CurrencyMaskPipe,
    MyCardNumberPipe,
  ]
})
export class DashboardModule { }



