import { Directive, HostListener, ElementRef, OnInit, Input } from "@angular/core";
import { MyCardNumberPipe } from "./pipe-card-number.pipe";


@Directive({ selector: "[MyCardNumberFormatter]" })
export class MyCardNumberFormatterDirective implements OnInit {

  private el: HTMLInputElement;

  constructor(
    private elementRef: ElementRef,
    private cardNumberPipe: MyCardNumberPipe
  ) {
    this.el = this.elementRef.nativeElement;

  }

  ngOnInit() {
    this.el.value = this.cardNumberPipe.transform(this.el.value, 2);
  }

  @HostListener("focus", ["$event.target.value"])
  onFocus(value) {
    this.el.value = this.cardNumberPipe.parse(value, 2); // opossite of transform
  }

  @HostListener("blur", ["$event.target.value"])
  onBlur(value) {
    this.el.value = this.cardNumberPipe.transform(value, 2);
  }

}


