import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';


@Component({
    templateUrl: 'transfer.component.html',
    providers: []
})
export class TransferComponent implements OnInit {
    
    submitted = false;
    hidden = false;

    secureCardNumber: number;
    beneficiaires: any = [];

    phone: string = "";
    account: string;
    firstname: string;
    lastname: string;
    label: string = "Montant";
    form: FormGroup;
    amountConfig: any = {};
    cardNumberConfig: any = {};
    cardTypeConfig: any = {};
    expirationDateConfig: any = {};
    cardCodeConfig: any = {};
    

    constructor(private router: Router, private fb: FormBuilder) {
        var address = JSON.parse(localStorage.getItem('address'));
        this.phone = address != null ? address.phone : "";
        var user = JSON.parse(localStorage.getItem('user'));
        this.firstname = user != null ? user.firstname : "";
        this.lastname = user != null ? user.lastname : "";
    }
    
    ngOnInit() {
        this.beneficiaires = JSON.parse(localStorage.getItem('beneficiaire'));
        this.initForm();
        this.cardNumberConfig = {
            form: this.form,
            control: this.form.get('cardNumber'),
            name: 'cardNumber'
        }
        this.cardTypeConfig = {
            form: this.form,
            control: this.form.get('cardType'),
            name: 'cardType'
        }
        this.amountConfig = {
            label: "Montant",
            form: this.form,
            control: this.form.get('amount'),
            name: 'amount'
        }
        this.expirationDateConfig = {
            form: this.form,
            control: this.form.get('cardExpiration'),
            name: 'cardExpiration'
        }
        this.cardCodeConfig = {
            form: this.form,
            control: this.form.get('cardCode'),
            name: 'cardCode'
        }

    }

    initForm() {
        this.form = this.fb.group({
            amount: [null, Validators.compose([Validators.required, Validators.pattern(/^(0|[1-9][0-9]*)$/i)])],
            cardNumber: ['', Validators.compose([Validators.required, Validators.pattern(/^([0-9]{16})*$/i)])],
            cardType: ["master-card", Validators.required],
            cardExpiration: [null, Validators.required],
            cardCode: ['', Validators.compose([Validators.required, Validators.pattern(/^([0-9]{3})*$/i)])],
            fullName: [this.firstname + ' ' + this.lastname, Validators.required],
        });
    }

    reset(): void {
        this.router.navigate(['/']);
    }
    private changeModel(ev) {

    }
    prev() {
        this.submitted = false;
        this.hidden === true;
    }

    transfer() {
        this.secureCardNumber = this.form.value.cardNumber.substr(-4);
        this.submitted = true;
    }

}




