import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Config } from './../../../../_helpers/config';

@Component({
  selector: 'expiration-date-input',
  templateUrl: './expiration-date-input.component.html',
  styleUrls: ['./expiration-date-input.component.css'],
  providers: []
})
export class ExpirationDateComponent implements OnInit {
  @Input('config')
  config: Config;

  years: any = [];
  months: any = [];
  date = new Date();
  m = this.date.getMonth();
  y = this.date.getFullYear();
  form1: FormGroup;
  form: FormGroup;
  control: FormControl;

  constructor(private fb: FormBuilder) {
    
    for (let i = this.m; i < 13; i++) {
      this.months.push({
        value: i, display: i < 10 ? "0" + i : i
      })
    }

    for (let i = this.y; i < 2030; i++) {
      this.years.push({
        value: i, display: i
      })
    }
  }

  ngOnInit() {
    this.initForm();
    this.form = this.config.form;
    this.control = this.config.control;
    this.control.setValue(this.date);
  }

  initForm() {
    this.form1 = this.fb.group({
      cardYear: [this.y, Validators.required],
      cardMonth: [this.m, Validators.required],
    });
  }

  onMonthChange(cardMonth) {
    this.date.setMonth(parseInt(cardMonth));
    this.control.setValue(this.date);
  }

  onYearChange(cardYear) {
     this.date.setFullYear(parseInt(cardYear));
    this.control.setValue(this.date);
  }
}



