import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { Config } from './../../../../_helpers/config';

@Component({
  selector: 'card-type',
  templateUrl: './card-type.component.html',
  styleUrls: ['./card-type.component.css'],
  providers: []
})
export class CardTypeComponent implements OnInit {

  public cards: any;
  form: FormGroup;
  control: FormControl;
  @Input('config')
  public config: Config;
  ngOnInit() {
    this.form = this.config.form;
    this.control = this.config.control;
    // this.control.setValue("master-card");
  }
  constructor() {
    this.cards = [
      { value: "master-card", display: "master-card", active: "" },
      { value: "visa", display: "visa", active: "" },
      { value: "amex", display: "amex", active: "" },
    ]
  }
}



