import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Config } from './../../../../_helpers/config';
@Component({
  selector: 'card-number',
  templateUrl: './card-number.component.html',
  styleUrls: ['./card-number.component.css'],
  providers: []
})
export class CardNumberComponent implements OnInit {
  cardLength: boolean = true;
  cards: any;
  form: FormGroup;
  control: FormControl;
  
  @Input('config')
  public config: Config;
  ngOnInit() {
    this.form = this.config.form;
    this.control = this.config.control;
  }

  onKey(event: any) { // without type info
    this.cardLength = false;
  }
  onPress(event: any) { // without type info
    this.cardLength = true;
  }
}



